import React, { Component } from 'react'
import './board.css'
export default class BoardAdmin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userData: '',
      kiemtrathongtin: false,
      xoathanhvien: true,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      themtailieu: false,
      xoatailieu: false,



      emailDelete: '',
      emailUpdate: '',
      sotienUpdate: '',
      emailNewPassHV: '',
      newPassHV: '',
      ttToan: false,
      ttVan: false,
      ttTienganh: false,
      ttSinh: false,
      ttLy: false,
      ttHoa: false,
      ttSu: false,
      ttDia: false,
      emailCapquyen: '',
      passAdminNew : '',
      mabimat: '',
      emailkiemtrathongtin: '',
      dataHocVien: '',
      titletailieu: '',
      linktailieu: '',
      linkanhtailieu: '',
      checktitlexoa: '',
      
    }
  }
  Quanlykhoahoca = (e) => { 
    e.preventDefault()
    console.log(" Click");
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     } else { 
      window.location.href = "/adminKhoahoc";
     }
  }

  kichhoatToan = () => { 
    this.setState({ 
      ttToan: true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Ton";
    ul.appendChild(li);
  }
  kichhoatVan = () => { 
    this.setState({ 
      ttVan: true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Vn";
    ul.appendChild(li);
  }

  kichhoatTienganh = () => { 
    this.setState({ 
      ttTienganh : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Ting Anh";
    ul.appendChild(li);
  }


  kichhoatLy = () => { 
    this.setState({ 
      ttLy : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "L";
    ul.appendChild(li);
  }

  kichhoatHoa = () => { 
    this.setState({ 
      ttHoa : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Ha";
    ul.appendChild(li);
  }

  kichhoatSinh = () => {
    this.setState({
      ttSinh : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Sinh";
    ul.appendChild(li);
  }
  kichhoatSu = () => {
    this.setState({
      ttSu : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "S";
    ul.appendChild(li);
  }

  kichhoatDia = () => {
    this.setState({
      ttDia : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "a";
    ul.appendChild(li);
  }

  displayXoaThanhVien = () => {
    this.setState({
      themtailieu: false,
      kiemtrathongtin: false,
      xoathanhvien: true,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      xoatailieu: false,
    })
  }

  displayCapNhatSoDu = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: true,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      xoatailieu: false,
      themtailieu: false,
    })
  }
  Kiemtradata = (e) => {
    this.setState({
     kiemtrathongtin: true,
     xoathanhvien: false,
     capnhatsodu: false,
     passHocvien: false,
     capquyenkhoahoc: false,
     passAdmin: false,
     xoatailieu: false,
     themtailieu: false,
   })
 }



  displaypassHocvien = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: true,
      capquyenkhoahoc: false,
      passAdmin: false,
      xoatailieu: false,
      themtailieu: false,
    })
  }

  displaytruycap = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: true,
      passAdmin: false,
      xoatailieu: false,
      themtailieu: false,
    })
  } 

   displaypassAdmin = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: true,
      themtailieu: false,
      xoatailieu: false,
    })
  }
  displaythemtailieu = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      themtailieu: true,
      xoatailieu: false,
    })
    
  }
  displayxoatailieu = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      themtailieu: false,
      xoatailieu: true,
    })

  }

  





  xoathanhvien = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     } else { 
   
    fetch("http://localhost:3005/api/xoathanhvien", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
        emailUser: this.state.emailDelete,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
       

        if (data.status === "ok") {


          alert("Xa Thnh Cng Hc Vin !");  // ng nhp thnh cng
          window.location.reload();
        } else {
          alert("C li xy ra! Vui lng th li.");

        }
      });
    }
  }


  componentDidMount() {
    const token = window.localStorage.getItem("tokenADMIN");
     if(!token){ 
      window.location.href = "/admin";
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     }  
  
    fetch("http://localhost:3005/api/BoardAdmin", {
      method: "POST",
      
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",

      },
      body: JSON.stringify({
        tokenADMIN: window.localStorage.getItem("tokenADMIN"),
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        
        this.setState({ userData: data.data });

      });
     
  }

  DangXuat = () => { 
    window.localStorage.removeItem("tokenADMIN");
    window.location.reload();
    window.location.href = "/";
    
  }






  capnhatsodu = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     } else {
    fetch("http://localhost:3005/api/capnhatsodu", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
        emailUser: this.state.emailUpdate,
        SoduCapnhat: this.state.sotienUpdate,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
       

        if (data.status === "ok") {


          alert(" Cp Nht S D Ca Hc Vin Thnh Cng !");  // ng nhp thnh cng
          window.location.reload();
        } else {
          alert("C li xy ra! Vui lng th li.");

        }
      });
    }
  }



  
  PassnewHocVien = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     } else {
    fetch("http://localhost:3005/api/capnhatpasssinhvien", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
        emailUser: this.state.emailNewPassHV,
        passwordCapnhat: this.state.newPassHV,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {


          alert(" Cp Nht Mt Khu Ca Hc Vin Thnh Cng !");  // ng nhp thnh cng
          window.location.reload();
        } else {
          alert("C li xy ra! Vui lng th li.");

        }
      });
    }
  }



  KichHoatKhoaHoc = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     } else { 

     
    fetch("http://localhost:3005/api/kichhoatkhoahoc", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
        emailUser: this.state.emailCapquyen,
        toan: this.state.ttToan,
        van: this.state.ttVan,
        tienganh: this.state.ttTienganh,
        sinh: this.state.ttSinh,
        ly: this.state.ttLy,
        hoa: this.state.ttHoa,
        su: this.state.ttSu,
        dia: this.state.ttDia,

      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {


          alert(" Cp Nht Cc Kha Hc Thnh Cng Ca Hc Vin Thnh Cng !");  // ng nhp thnh cng
          window.location.reload();
        } else {
          alert("C li xy ra! Vui lng th li.");

        }
      });
    }
  }


  DoipassAdmin = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     } else {
    fetch("http://localhost:3005/api/changepasswordAdmin", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
       tokenADMIN: window.localStorage.getItem("tokenADMIN"),
       passwordCapnhat: this.state.passAdminNew,
       Mabimat: this.state.mabimat,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
     

        if (data.status === "ok") {


          alert(" Cp Nht Cc Kha Hc Thnh Cng Ca Hc Vin Thnh Cng !");  // ng nhp thnh cng
          window.location.reload();
        } else {
          alert("C li xy ra! Vui lng th li.");

        }
      });
    }
  }

  Kiemtrathongtinhocvien = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     } else {
    fetch("http://localhost:3005/api/kiemtrathongtinhocsinh", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
       tokenADMIN: window.localStorage.getItem("tokenADMIN"),
       emailUser: this.state.emailkiemtrathongtin,
      
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {
          this.setState({
            dataHocVien: data.data,
          })
       
         
        } else {
          alert("C li xy ra! Vui lng th li.");

        }
      });
    }
  }


  Themtailieu = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     } else {
    fetch("http://localhost:3005/api/themtailieu", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
       tokenADMIN: window.localStorage.getItem("tokenADMIN"),
       title: this.state.titletailieu,
      linkimg: this.state.linkanhtailieu,
      linkdownloadfn: this.state.linktailieu, 
      
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {
          alert(" Thm Ti Liu Thnh Cng !");  // ng nhp thnh cng
          window.location.reload();
       
         
        } else {
          alert("C li xy ra! Vui lng th li.");

        }
      });
    }
  }



  Xoatailieu = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("ng Nhp i Ri Anh Cho Vo Nha B ! ")
     } else {
    fetch("http://localhost:3005/api/xoatailieu", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
       tokenADMIN: window.localStorage.getItem("tokenADMIN"),
       checktitlexoa: this.state.checktitlexoa,
     
      
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {
          alert(" Xa Ti Liu Thnh Cng !");  // ng nhp thnh cng
          window.location.reload();
       
         
        } else {
          alert("C li xy ra! Vui lng th li.");

        }
      });
    }
  }

 

  



  render() {
    return (
      <div>

        <div className="parentad">
          <div className="div1ad">
            <h1>TRANG QUN L</h1>
            <ul>
              <li> <i className="fa-solid fa-landmark" /> Doanh Thu: {this.state.userData.doanhthu}</li>
              <li><i className="fa-solid fa-users" />S Lng Thnh Vin: {this.state.userData.danso} </li>

            </ul>
            <p>phn ny bn ch cn bn vo cc la chn bn di l n hin ra bng x l nha !<br></br>Nu chnh sai hay sao th c biu ti nh, ti vo code ti sa li cho </p>
            <button onClick={this.DangXuat}>ng Xut</button>
          </div>
          <div className="div2ad">
            <div className="listButton">
             
              <button onClick={this.displayXoaThanhVien}>Xa Thnh Vin</button>
              <button onClick={this.displayCapNhatSoDu}>Cp Nht S D</button>
              <button onClick ={this.displaypassHocvien}>Mt Khu Hc Sinh</button>
              <button onClick ={this.displaytruycap}>Cp Quyn Kha Hc</button>
              <button onClick= {this.displaypassAdmin}>Thay Mt Khu ADMIN</button>
              <button onClick= {this.Kiemtradata}>Kim Tra Thng Tin Hc Sinh</button>
              <button onClick={this.displaythemtailieu}>Thm Ti Liu</button>
              <button onClick={this.displayxoatailieu} >Xa Ti Liu</button>
              <button onClick={this.Quanlykhoahoca}>Qun L Kha Hc</button>
             
            </div>
          </div>
          <div className="div3ad">


            {/* Xa Thnh Vin */}
            {this.state.xoathanhvien ?
              <div className="xoathanhvien">
                <h1>Xo Thnh Vin</h1>
                <form onSubmit={this.xoathanhvien}>
                  <input type="text" className="form-control" placeholder="Nhp Email Ca Hc Vin" onChange={(e) => { this.setState({ emailDelete: e.target.value }); }} />
                  <button type="submit">Xa Ngay</button>
                </form>
              </div> : null}

            {/* Cp Nht S D */}
            {this.state.capnhatsodu ?
              <div>

                <form onSubmit={this.capnhatsodu}>
                  <input type="text" className="form-control" placeholder="Nhp Email Ca Hc Vin" onChange={(e) => { this.setState({ emailNewPassHV: e.target.value }); }} />
                  <input type="text" className="form-control" placeholder="Nhp S Tin Cn Cp Nht " onChange={(e) => { this.setState({ sotienUpdate: e.target.value }) }} />
                  <button type="submit">Cp Nht Ngay</button>
                </form>

              </div>
              : null}

              {/* Thay Mt Khu Hc Sinh */}

              {this.state.passHocvien ? 
              <div> 
                <h1>Thay i Mt Khu Hc Sinh</h1>
                <form onSubmit ={this.PassnewHocVien}>
                <input type="text" className="form-control" placeholder="Nhp Email Ca Hc Vin" onChange={(e) => { this.setState({ emailNewPassHV: e.target.value }); }} />
                <input type="text" className="form-control" placeholder="Mt Khu Mi Cho Hc Sinh" onChange={(e) => { this.setState({ newPassHV: e.target.value }); }} />
                 <button type="submit">Cp Nht Ngay</button>
                </form>
              </div>
              :null}



            {/* Cp Quyn Truy Cp Kha Hc */}
            {this.state.capquyenkhoahoc ? 
            <div  className ="themkhoahoc"> 
              <h1>Cp Quyn Truy Cp Kha Hc</h1>

              <form onSubmit={this.KichHoatKhoaHoc} > 
                <input type="text" className="form-control" placeholder="Nhp Email Ca Hc Vin" onChange={(e) => { this.setState({ emailCapquyen: e.target.value }); }} />
              <button type = "submit">Kch Hot Ngay</button>
              </form>
                <ul>
                  <li><a  onClick={this.kichhoatToan}>Thm Ton</a></li>
                  <li><a onClick ={this.kichhoatVan}>Thm Ng Vn</a></li>
                  <li><a onClick = {this.kichhoatTienganh}>Thm Ting Anh</a></li>
                  <li><a onClick ={this.kichhoatLy}>Thm Vt L</a></li>
                  <li><a onClick={this.kichhoatHoa}>Thm Ha</a></li>
                  <li><a onClick={this.kichhoatSinh}>Thm Sinh Hc</a></li>
                  <li><a onClick ={this.kichhoatSu}>Thm Lch S</a></li>
                  <li><a onClick = {this.kichhoatDia}>Thm a L</a></li>
                </ul>
                
                <ul id="Listkhoahoc">
                <h1 className ="text-center text-light">Danh Sch Cc Kha Hc c Thm Vo</h1>
                </ul>

              
            </div>:null}

             {/* I MT KHU ADMIN */}
             {this.state.passAdmin ? 
            <div>
              <h1>i Mt Khu ADMIN QUN L</h1>
              <form onSubmit ={this.DoipassAdmin}>
               
                <input type = "text" className = "form-control" placeholder= "Nhp Mt Khu Mi" onChange ={(e) => {this.setState({passAdminNew:e.target.value})}} />
                <input type = "text" className ="form-control" placeholder= "Nhp Kha B Mt"  onChange ={(e) => {this.setState({mabimat:e.target.value})}} />
                <button type="submit" className= "btn btn-success">i Mt Khu</button>
              </form>
            </div>
             :null}

             {/* KIM TRA THNG TIN HC SINH */}
           {this.state.kiemtrathongtin ? <div>
            <form onSubmit ={this.Kiemtrathongtinhocvien}>
               
               <input type = "text" className = "form-control" placeholder= "Nhp Email Hc Sinh Cn Kim Tra " onChange ={(e) => {this.setState({emailkiemtrathongtin:e.target.value})}} />
              
               <button type="submit" className= "btn btn-success">Tra Cu Ngay</button>
             </form>
             <ul style ={{width:"100%",height:"100%",display:"flex",flexDirection:"column",justifyContent:"center",alignItems:"center"}}>
              <h3>Thng Tin Ca Hc Sinh</h3>
              <li>Email ng K: {this.state.dataHocVien.email } </li>
              <li>S D: {this.state.dataHocVien.sodu }</li>
              <li>Tn Ti Khon: {this.state.dataHocVien.username}</li>
              <li>Mn Ton: {this.state.dataHocVien.checkToan ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
              <li>Mn Vn: {this.state.dataHocVien.checkVan ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>} </li>
              <li>Mn Ting Anh: {this.state.dataHocVien.checkTienganh ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
              <li>Mn Sinh: {this.state.dataHocVien.checkSinh ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
               <li> Mn L: {this.state.dataHocVien.checkLy ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
               <li> Mn Ha: {this.state.dataHocVien.checkHoa ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
               <li> Mn a: {this.state.dataHocVien.checkDia ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
               <li> Mn S: {this.state.dataHocVien.checkSu ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
             </ul>
           </div>:null}

           {/* THM TI LIU */}

           {this.state.themtailieu ? <div>
            <h1>Thm Ti Liu</h1>
              <form onSubmit ={this.Themtailieu}>
               
                <input type = "text" className = "form-control" placeholder= "Nhp Tiu  " onChange ={(e) => {this.setState({titletailieu:e.target.value})}} />
                <input type = "text" className ="form-control" placeholder= "Nhp Link nh"  onChange ={(e) => {this.setState({linkanhtailieu:e.target.value})}} />
                <input type = "text" className ="form-control" placeholder= "Nhp Link Ti "  onChange ={(e) => {this.setState({linktailieu:e.target.value})}} />
                <button type="submit" className= "btn btn-success">Thm Ti Liu</button>
              </form>
           </div>: null}

           {/* XOA TAI LIEU */}
           {this.state.xoatailieu ? 
           <div>
              <h1>Xa Ti Liu</h1>
              <form onSubmit ={this.Xoatailieu}>
                <input type = "text" className = "form-control" placeholder= "Nhp Title Ti Liu Cn Xa" onChange ={(e) => {this.setState({checktitlexoa:e.target.value})}} />
                <button type="submit" className= "btn btn-success">Xa Ti Liu</button>
              </form>
           </div>
           :null}
            
      


          </div>
        </div>




      </div>
    )
  }
}
